import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import { Container, Row, Col, Card, Table } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";

import Loader from '../../components/loader/Loader';
// filter components
import { useWidowResize } from '../../constants/utilityHooks';
import { optionMeasure, optionRange, optionGroup, optionRegion, optionMetro, optionVertical, optionRanking, optionHyperscaler } from '../../constants/filterOptions';
import Separator from '../../components/customDropdown/Separator'
import RadioDD from '../../components/customDropdown/RadioDD'
import CheckboxDD from '../../components/customDropdown/CheckboxDD'
import ResponsiveRadioDD from '../../components/customDropdown/ResponsiveRadioDD';
import SearchCheckboxDD from '../../components/customDropdown/SearchCheckboxDD';
import FilterContainer from '../../components/filter/FilterContainer';
import FilterModal from '../../components/filter/FilterModal';

const Customer = () => {
    const [isLoading, setLoadingStatus] = useState(true);

    useEffect(() => {
        let isMounted = true;

        const fetchData = async () => {
            // Check if component is still mounted
            if (isMounted) {
                setLoadingStatus(false);
            }
        };
        fetchData();

        return () => {
            isMounted = false;
        };
    }, []);

    const cardRef = useRef();
    const { width } = useWidowResize(cardRef);
  
    const [showModal, setShowModal] = useState(false);
    const handleCloseModal = () => {
      setShowModal(false);
    }
    const handleShowModal = () => {
      setShowModal(true)
    };

    if (isLoading) return <div ref={cardRef}><Loader /></div>;

    return (
        <Container className="customer" fluid ref={cardRef}>
<FilterContainer 
      className={`${width}`} 
      handleShowModal={handleShowModal}
      >
        <CheckboxDD
            title="Region"
            id="measeure"
            options={optionRegion}
          />
          <SearchCheckboxDD
            title="Metro"
            id="metro"
            options={optionMetro}
          />
          <RadioDD
            title="Vertical"
            id="vertical"
            options={optionVertical}
          />
          <RadioDD
            title="Measure"
            id="measeure"
            options={optionMeasure}
          />
          <Separator height='39px' />
          <ResponsiveRadioDD
            title="Range by"
            id="range2"
            wide={width >= 1394 ? true : false}
            narrow={width < 1394 ? true : false}
            options={optionRange}
          />
          <Separator height='39px' />
          <ResponsiveRadioDD
            id="hyperscaler"
            addTitleOnWide
            wide={width >= 1516 ? true : false}
            narrow={width < 1516 ? true : false}
            options={optionHyperscaler}
          />

          <FilterModal
            title="Topline"
            setShowModal={setShowModal}
            showModal={showModal}
            handleCloseModal={handleCloseModal}
          >
            <CheckboxDD
              title="Region"
              id="measeure"
              options={optionRegion}
              visibleInModal
            />
            <SearchCheckboxDD
              title="Metro"
              id="metro"
              options={optionMetro}
              visibleInModal
            />
            <RadioDD
                title="Vertical"
                id="vertical"
                options={optionVertical}
                visibleInModal
            />
            <RadioDD
              title="Measure"
              id="measeure"
              options={optionMeasure}
              visibleInModal
            />
            <ResponsiveRadioDD
              title="Range by"
              id="range2"
              options={optionRange}
              visibleInModal
            />
            <ResponsiveRadioDD
              title="Group by"
              id="group"
              options={optionGroup}
              visibleInModal
            />
            <RadioDD
                id="ranking"
                options={optionRanking}
                visibleInModal
            />
          </FilterModal>
      </FilterContainer>

            <Row>
                <Col md={8}>
                    <Card className="revenue-card">
                        <Card.Body>
                            <Card.Title>Top 10 Customers by MRR</Card.Title>
                            insert stacked column bar chart here
                        </Card.Body>
                    </Card>
                </Col>
                <Col md={4}>
                    <Card className="revenue-card">
                        <Card.Body>
                            <Card.Title>Top 10 Customers by MRR</Card.Title>
                            insert line chart here
                        </Card.Body>
                    </Card>
                </Col>
            </Row>
            <Row>
                <Col md={5}>
                    <Card className="revenue-card">
                        <Card.Body>
                            <Card.Title>Top 10 Customers by MRR</Card.Title>
                            insert deployment map here
                        </Card.Body>
                    </Card>
                    
                </Col>
                <Col md={7}>
                    <Card className="revenue-card">
                        <Card.Body>
                            <Card.Title>Top 10 Customers by MRR</Card.Title>
                            insert customer ranking table here
                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </Container >
    );
};

export default Customer;
