import React, { useState } from 'react';
import { Dropdown } from 'react-bootstrap';
import { BiChevronDown } from 'react-icons/bi';
import './customDropdown.scss';


const ResponsiveBasicDD = ({ options, wide, narrow, className, id }) => {
  const initialValue = options ? options[0].text : 'try adding something to click to, use "option" props.'
  const [value, setValue] = useState(initialValue);
  
  const handleSelect = (e) => {
    setValue(e)
  }

  return (
    <>
      <ul className={`responsive-basic-dd wide-list ${wide ? 'visible' : 'hidden'} ${className ? className : ''}`}>
        {options
        ? <>
          {options.map(option => (
            <li key={option.key}
              onClick={() => handleSelect(option.text)}
              className={`${option.text === value ? 'active' : ''}`}
            >
              {option.text}
            </li>
          ))}
          </>
        : <li>{initialValue}</li> 
        }
      </ul>

      <Dropdown className={`responsive-basic-dd narrow-list ${narrow ? 'visible' : 'hidden'} ${className ? className : ''}`}>
        <Dropdown.Toggle id={`responsive_basic_dd-${id}`} variant='light'>
          {value} <BiChevronDown viewBox="6 8 12 12" />
        </Dropdown.Toggle>

        <Dropdown.Menu>
          {options && options.map(option => (
            <Dropdown.Item
              key={option.key}
              eventKey={option.key}
              onClick={() => handleSelect(option.text)}
            >
              {option.text}
            </Dropdown.Item>
          ))}
        </Dropdown.Menu>
      </Dropdown>
    </>
  );
}

export default ResponsiveBasicDD;
