import React from 'react';
import { useHistory, Link } from 'react-router-dom';
import { Row, Col, Form, Button } from 'react-bootstrap';
import { ReactComponent as BrandLogo } from '../../assets/images/atomnew.svg';

import './login.scss';

const Login = () => {
    let history = useHistory();

    const handleSubmit = () => {
		history.push("/");
    }
    return(
        <div className="Login">
            <div className="Form">
                <BrandLogo className="Logo" />
                <div className="SpanMessage">
                    <span>Welcome back! Please login to your account.</span>
                </div>
                <Form className="Fields">
                    <Form.Group id="input_email">
                        <Form.Control type="email" placeholder="Email" maxLength="50" autoComplete="off"></Form.Control>
                    </Form.Group>
                    <Form.Group id="input_password">
                        <Form.Control type="password" placeholder="Password" maxLength="50"></Form.Control>
                    </Form.Group>
                    <Form.Group as={Row} className="inline-components">
                        <Col>
                            <Form.Check className="custom-checkbox">
                                <Form.Check.Input id="checkbox_remember_me" className="custom-control-input"></Form.Check.Input>
                                <Form.Check.Label htmlFor="checkbox_remember_me" className="custom-control-label unselectable">Remember Me</Form.Check.Label>
                            </Form.Check>
                        </Col>
                        <Col>
                            <Button id="btn_forget_password" variant="link">Forgot Password?</Button>
                        </Col>
                    </Form.Group>
                    <Form.Group as={Row} className="inline-buttons">
                        <Col style={{paddingRight: "10px"}}>
                            <Button id="btn_login" className="btn-block" variant="primary" onClick={handleSubmit}>Login</Button>
                        </Col>
                        <Col style={{paddingLeft: "10px"}}>
                            <Link to="/signup" id="btn_request_access" className="btn-block" variant="light">signup</Link>
                        </Col>
                    </Form.Group>
                </Form>
            </div>
        </div>
    );
};

export default Login;
